.modal {
  width: 728px;
  background-color: $light-grey;
  color: $dark2;
  letter-spacing: normal;
  text-align: center;
  z-index: -1;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  opacity: 0;
  display: none;

  &.active {
    z-index: 20;
    position: absolute;
    opacity: 1;
    display: block;
  }
}

.modal__btn-close {
  position: absolute;
  top: 0;
  right: 0;
  padding: 20px;
  background-color: transparent;
  border: none;
  cursor: pointer;

  &:hover {
    outline: 1px solid rgba($dark, 0.2);
    border-radius: 50%;
  }
}

.modal-buy {
  padding-left: 338px;
  background:
    url("../img/form-buy.jpg") no-repeat left center,
    linear-gradient(to left, $light-grey, $light-grey 100%);
  background-size: 338px 444px;

  @media only screen and (min-resolution: 2dppx) {
    background:
      url("../img/form-buy@2x.jpg") no-repeat left center,
      linear-gradient(to left, $light-grey, $light-grey 100%);
    background-size: 338px 444px;
  }
}

.modal-buy__content {
  padding: 68px 61px 75px 59px;
}

.modal-buy__title {
  @include font-size(25px, 43px, 600);
  margin-bottom: 50px;
}

.modal-buy__label {
  color: $dark2;
}

.modal-buy__input {
  box-shadow: 0 0 1px #d7d9df;
  color: $dark2;
}

.modal-buy__btn {
  padding: 18px 40px;
}

.modal-message {
  @include font-size(25px, 43px, 500);
  align-items: center;
  justify-content: center;
  display: none;
  height: 444px;

  &.active {
    z-index: 25;
    display: flex;
  }
}

/* QUERRIES
===================================================================== */
@media screen and (max-width: $screen--sm) {
  .modal-buy {
    width: 320px;
    padding-left: 0;
  }

  .modal-buy__content {
    min-height: 568px;
    padding: 100px $gap--sm 60px;
    padding-left: $gap--sm;
    padding-right: $gap--sm;
    background: $light-grey;
  }

  .modal-buy__label {
    color: $dark;
  }

  .modal-buy__input {
    width: 288px;
    box-shadow: 0 0 1px $grey--30;
    color: $dark;
  }

  .modal-message {
    width: 288px;
  }
}
