.countries {
  height: 1230px;
  position: relative;
  padding-right: 0;

  &::after {
    content: "";
    z-index: -1;
    position: absolute;
    top: 0;
    left: -9%;
    width: 1444px;
    height: 100%;
    background: url("../img/countries.png") no-repeat center 140px;
    background-size: 1444px 1089px;

    @media only screen and (min-resolution: 2dppx) {
      background: url("../img/countries@2x.png") no-repeat center;
      background-size: 1444px 1089px;
    }
  }
}

.countries__list {
  @include reset-list();
  display: flex;
  flex-wrap: nowrap;
  white-space: nowrap;
  align-items: center;
  width: 899px;
  overflow-x: auto;
  margin-bottom: 75px;
  box-shadow: 0 1px 0 $grey--30;
  -webkit-overflow-scrolling: touch;
  -ms-overflow-style: none;
  scrollbar-width: none;

  &::-webkit-scrollbar {
    height: 0;
  }
}

.countries__list-item {
  margin-right: 99px;
}

.countries__list-item-link {
  @include font-size(20px, 24px, 500);
  letter-spacing: -0.41px;
  cursor: pointer;
  color: $grey--30;
  padding: 15px 0;
  display: block;

  &:hover {
    color: $theme;
  }

  &:active {
    box-shadow: 0 2px 0 $theme;
  }

  &:target {
    color: $dark--70;
  }

  &:target:hover {
    color: $theme;
  }

  &.active {
    pointer-events: none;
    color: $theme;
  }
}

.countries__items {
  display: grid;
  grid-template-columns: 1fr;
  padding-right: $gap--lg;
}

.countries__item {
  z-index: -1;
  opacity: 0;
  transition: all 0.6s ease-in-out;
  grid-column: 1 / span 1;
  grid-row: 1 / span 1;
  display: flex;
  justify-content: space-between;

  &.active {
    z-index: 1;
    opacity: 1;
  }

  &--greece {
    background: url("../img/greece/greece--lg.jpg") no-repeat 62% center / contain;

    @media only screen and (min-resolution: 2dppx) {
      background: url("../img/greece/greece@2x--lg.jpg") no-repeat 62% center / contain;
    }
  }

  &--albania {
    background: url("../img/albania/albania--lg.jpg") no-repeat 62% center / contain;

    @media only screen and (min-resolution: 2dppx) {
      background: url("../img/albania/albania@2x--lg.jpg") no-repeat 62% center / contain;
    }
  }

  &--macedonia {
    background: url("../img/macedonia/macedonia--lg.jpg") no-repeat 62% center / contain;

    @media only screen and (min-resolution: 2dppx) {
      background: url("../img/macedonia/macedonia@2x--lg.jpg") no-repeat 62% center / contain;
    }
  }

  &--montenegro {
    background: url("../img/montenegro/montenegro--lg.jpg") no-repeat 62% center / contain;

    @media only screen and (min-resolution: 2dppx) {
      background: url("../img/montenegro/montenegro@2x--lg.jpg") no-repeat 62% center / contain;
    }
  }

  &--croatia {
    background: url("../img/croatia/croatia--lg.jpg") no-repeat 62% center / contain;

    @media only screen and (min-resolution: 2dppx) {
      background: url("../img/croatia/croatia@2x--lg.jpg") no-repeat 62% center / contain;
    }
  }
}

.countries__desc {
  width: 370px;
}

.countries__text {
  margin-top: 0;
  margin-bottom: 50px;
}

.countries__review {
  width: 382px;
  padding: 30px;
  align-self: center;
  background-color: $white;
}

.countries__review-title {
  @include font-size(30px, 37px, 500);
  letter-spacing: -0.69px;
  color: $grey;
  position: relative;
  margin-top: 0;
  margin-bottom: 20px;
  padding-bottom: 10px;

  &::after {
    content: "";
    position: absolute;
    bottom: 0;
    left: 0;
    height: 1px;
    width: 68px;
    background-color: $theme;
  }
}

.countries__review-text {
  margin-top: 0;
  margin-bottom: 35px;
  font-size: 14px;
  letter-spacing: -0.41px;
}

.countries__review-client {
  @include font-size(16px, 20px, 500);
  color: $grey;
  margin: 0;
  display: flex;
  align-items: center;
}

.countries__review-client picture,
.countries__review-client img {
  width: 38px;
  height: 38px;
  margin-right: 16px;
  border-radius: 50%;
}

.countries__review-client figcaption {
  width: calc(100% - 38px - 16px);
}


/* QUERRIES
===================================================================== */
@media screen and (max-width: $screen--md) {
  .countries {
    height: auto;
    margin-bottom: 64px;
    padding-left: 0;

    &::after {
      display: none;
    }
  }

  .countries__list {
    width: 747px;
    margin-bottom: 30px;
    margin-left: $gap--md;
  }

  .countries__items {
    padding-right: 0;
  }

  .countries__item {
    flex-direction: column;

    &--greece {
      background: url("../img/greece/greece--xl.jpg") no-repeat left 333px / contain;

      @media only screen and (min-resolution: 2dppx) {
        background: url("../img/greece/greece@2x--xl.jpg") no-repeat left 333px / contain;
      }
    }

    &--albania,
    &--macedonia,
    &--montenegro,
    &--croatia {
      background-size: cover;
      background-position: left 333px;
    }
  }

  .countries__desc {
    width: 100%;
    margin-bottom: 110px;
    padding-left: $gap--md;
    padding-right: $gap--md;
    text-align: center;
  }

  .countries__review {
    padding-top: 33px;
  }

  .countries__text {
    margin-bottom: 20px;
  }
}

@media screen and (max-width: $screen--sm) {
  .countries {
    margin-bottom: 34px;
  }

  .countries__list {
    width: 304px;
    margin-bottom: 24px;
    margin-left: $gap--sm;
  }

  .countries__item {
    &--greece,
    &--albania,
    &--macedonia,
    &--montenegro,
    &--croatia {
      background-size: 100% 465px;
      background-position: center bottom;
    }
  }

  .countries__desc {
    padding-left: $gap--sm;
    padding-right: $gap--sm;
    margin-bottom: 100px;
  }

  .countries__review {
    width: 268px;
    padding-left: 20px;
    padding-right: 20px;
    text-align: center;
  }

  .countries__review-title {
    font-size: 20px;
    line-height: 24px;

    &::after {
      left: 50%;
      transform: translateX(-50%);
    }
  }

  .countries__review-text {
    margin-bottom: 20px;
  }

  .countries__review-client {
    flex-wrap: wrap;
    justify-content: center;
  }

  .countries__review-client img,
  .countries__review-client picture {
    margin-right: 0;
    margin-bottom: 10px;
  }

  .countries__review-client figcaption {
    width: 100%;
  }
}
