@include title-keyframes(title1, 229px);

.places {
  margin-bottom: 110px;
}

.places__desc {
  width: 350px;
  margin-bottom: 55px;
}

.places__title {
  @include title-animation(4px, title1);
  line-height: 80px;
  padding-bottom: 30px;
}

.places__text {
  margin-top: 0;
}

.places__items {
  display: grid;
  grid-template-columns: repeat(15, 1fr);
  gap: 100px 60px;
}

.places__item {
  margin: 0;
  grid-column: span 5;
  position: relative;
  overflow: hidden;
  line-height: 0;

  &:hover .places__item-link {
    left: 0;
  }
}

.places__item:last-child:nth-child(3n - 1) {
  grid-column-end: -3;
}

.places__item:nth-last-child(2):nth-child(3n + 1) {
  grid-column-end: 8;
}

.places__item:last-child:nth-child(3n - 2) {
  grid-column-end: 11;
}

.places__item-link {
  @include font-size(14px, 17px, 500);
  text-decoration: none;
  text-transform: uppercase;
  letter-spacing: 2px;
  color: $white;
  z-index: 2;
  position: absolute;
  top: 0;
  left: 100%;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: rgba($dark, 0.6);
  transition: all 0.6s ease;

  &:active .places__item-link-text {
    box-shadow: 0 0 2px $white;
  }
}

.places__item-link-text {
  display: block;
  box-shadow: 0 0 1px rgba($white, 0.7);
  padding: 30px 25px;
  text-align: center;
}

.places__item-name {
  @include font-size(24px, 29px, 500);
  color: $grey;
  letter-spacing: -0.41px;
  z-index: 0;
  position: absolute;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%);
  width: 215px;
  padding: 17px 30px;
  text-align: center;
  background-color: $white;
}


/* QUERRIES
===================================================================== */
@media screen and (max-width: $screen--md) {
  .places {
    margin-bottom: 80px;
    padding-left: 76px;
    padding-right: 76px;
  }

  .places__desc {
    margin: 0 auto 40px;
    text-align: center;
  }

  .places__title {
    line-height: 45px;
    padding-bottom: 0;
  }

  .places__items {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap: 40px;
  }

  .places__item picture,
  .places__item img {
    width: 100%;
    height: auto;
  }
}

@media screen and (max-width: $screen--sm) {
  .places {
    margin-bottom: 25px;
    padding-left: $gap--sm;
    padding-right: $gap--sm;
  }

  .places__desc {
    width: 288px;
    margin-bottom: 20px;
  }

  .places__title {
    line-height: 36px;
  }

  .places__items {
    gap: 35px;
  }
}
