.form-group {
  position: relative;
  margin-bottom: 20px;
}

.input {
  @include font-size(14px, 17px, 400);
  font-family: inherit;
  color: $dark;
  letter-spacing: normal;
  box-shadow: 0 0 1px $grey--30;
  border: none;
  padding: 16px 15px 15px 62px;
  width: 269px;
  appearance: none;

  &:hover {
    box-shadow: 0 0 1px $dark;
  }

  &.error {
    box-shadow: 0 0 6px 6px $theme;
  }
}

.label {
  @include font-size(14px, 17px, 400);
  color: $dark;
  letter-spacing: normal;
  position: absolute;
  top: 15px;
  left: 13px;
}

.label-phone {
  top: 7px;
}

.label sup {
  @include font-size(17px, 21px, 700);
  color: $theme;
}
