.btn {
  @include font-size(12px, 15px, 700);
  display: inline-block;
  padding: 30px 40px;
  color: $white;
  text-align: center;
  border: none;
  background-color: $theme;
  font-family: inherit;
  letter-spacing: 2px;
  text-transform: uppercase;
  text-decoration: none;
  cursor: pointer;

  &:hover {
    background-color: $accent;
  }

  &:active {
    background-color: $accent;
  }
}

.phone {
  @include font-size(18px, 32px, 400);
  color: $dark;
  letter-spacing: normal;
  white-space: nowrap;
  display: flex;
  flex-wrap: nowrap;
  align-items: center;

  &:hover {
    color: $theme;
  }

  &:active {
    text-decoration: none;
  }
}

.phone svg {
  margin-right: 6px;
  fill: none;
}

/* QUERRIES
===================================================================== */
@media screen and (max-width: $screen--md) {
  .btn {
    @include font-size(10px, 12px, 500);
    width: 250px;
    margin-left: auto;
    margin-right: auto;
    padding-top: 19px;
    padding-bottom: 19px;
  }
}

@media screen and (max-width: $screen--sm) {
  .btn {
    width: 288px;
  }
}
