@mixin title-animation($h, $name) {
  &::after {
    height: $h;
    animation: 2s ease-in forwards $name;
  }
}

@mixin title-keyframes($name, $w) {
  @keyframes #{$name} {
    from {
      width: 0;
    }

    to {
      width: $w;
    }
  }
}

@include title-keyframes(title, 175px);


.title {
  @include font-size(55px, 90px, 600);
  position: relative;
  letter-spacing: -1.25px;
  color: $grey;
  margin-top: 0;
  margin-bottom: 30px;
  padding-bottom: 20px;

  &::after {
    content: "";
    position: absolute;
    bottom: 0;
    left: 0;
    background-color: $theme;
  }

  @include title-animation(4px, title);
}

/* QUERRIES
===================================================================== */
@media screen and (max-width: $screen--md) {
  .title {
    font-size: 30px;
    line-height: 45px;
    letter-spacing: normal;
    text-align: center;
    margin-bottom: 20px;
    padding-bottom: 0;

    &::after {
      display: none;
    }
  }
}

@media screen and (max-width: $screen--sm) {
  .title {
    font-size: 24px;
    line-height: 36px;
  }
}
