// VARS
//=====================================================================
// COLORS
$white: #ffffff;
$black: #000000;
$theme: #fe7865;
$accent: #f17d4b;
$error: $theme;
$dark: #141517;
$dark--70: rgba(#141517, 0.7);
$dark--50: rgba(#141517, 0.5);
$grey: #2c2e3f;
$grey--70: rgba(#2c2e3f, 0.7);
$grey--30: rgba(#2c2e3f, 0.3);
$dark2: #48494d;
$light-grey: #fbf8f8;

// LAYOUT
$screen--sm: 767px;
$screen--md: 1023px;
$screen--lg: 1240px;
$min-width: 320px;
$max-width: 1920px;

$gap--sm: 16px;
$gap--md: 21px;
$gap--lg: 35px;

// TEXT
$font: "Montserrat", system-ui, -apple-system, "Noto Sans", sans-serif;


// MIXINS
//=====================================================================
@mixin font-size($fz, $lh, $fw) {
  font-size: $fz;
  line-height: $lh;
  font-weight: $fw;
}

@mixin reset-list() {
  margin: 0;
  padding: 0;
  list-style: none;
}

@mixin center($max, $gap, $margin: auto) {
  width: 100%;
  max-width: $max;
  margin: 0 $margin;
  padding: 0 $gap;
}
