.footer {
  @include font-size(14px, 17px, 400);
  color: $dark--50;
  letter-spacing: normal;
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  padding-top: 60px;
  padding-bottom: 40px;
}

.footer__block {
  margin-bottom: 15px;
}

.footer__block-title {
  @include font-size(18px, 32px, 600);
  color: $dark;
  margin-top: 0;
  margin-bottom: 20px;
  width: 100%;
}

.footer__trips {
  width: 33.07%;
  flex-grow: 1;
  display: flex;
  flex-wrap: wrap;
  align-items: flex-start;
  justify-content: space-between;
  padding-right: 37px;
  box-shadow: 1px 0 0 #d7d9df;
}

.footer__trips-list {
  @include reset-list();
  margin-top: -36px;
}

.footer__trips-item:not(:last-child) {
  margin-bottom: 10px;
}

.footer__contacts {
  width: 397px;
  padding-left: 65px;
  padding-right: 65px;
  box-shadow: 1px 0 0 #d7d9df;
}

.footer__contacts-title {
  margin-bottom: 25px;
}

.footer__contacts-link {
  margin-bottom: 21px;
  display: flex;
  align-items: center;
  color: $theme;

  &:hover svg {
    color: $theme;
  }

  &:active {
    text-decoration: none;
  }

  &:active svg {
    color: $theme;
  }
}

.footer__contacts-link--phone {
  font-size: 16px;
}

.footer__contacts-link svg {
  color: #d7d9df;
  margin-right: 15px;
}

.footer__extras {
  width: 32.05%;
  flex-grow: 1;
  padding-left: 58px;
}

.footer__extras-text {
  margin-top: 0;
  line-height: 24px;
}

/* QUERRIES
===================================================================== */
@media screen and (max-width: $screen--md) {
  .footer {
    padding-top: 20px;
    padding-bottom: 10px;
  }

  .footer__block {
    margin-bottom: 30px;
  }

  .footer__trips {
    width: 54%;
    padding-right: 50px;
  }

  .footer__trips-list {
    margin-top: 0;
  }

  .footer__contacts {
    width: 46%;
    padding-left: 50px;
    padding-right: 0;
    box-shadow: none;
  }

  .footer__extras {
    padding-top: 30px;
    padding-left: 0;
    box-shadow: 0 -1px 0 #d7d9df;
    text-align: center;
  }

  .footer__contacts-link {
    margin-bottom: 23px;
  }

  .footer__contacts-link--mail {
    text-decoration: none;
  }
}

@media screen and (max-width: $screen--sm) {
  .footer {
    flex-direction: column;
  }

  .footer__block-title {
    width: 100%;
    text-align: center;
  }

  .footer__trips {
    padding-right: 0;
    padding-bottom: 10px;
    width: auto;
    justify-content: center;
    box-shadow: 0 1px 0 #d7d9df;
  }

  .footer__trips-list {
    width: 124px;
    margin: 0 10px 20px;
    text-align: center;
  }

  .footer__contacts {
    width: auto;
    padding-left: 0;
    text-align: center;
    box-shadow: 0 1px 0 #d7d9df;
  }

  .footer__contacts-link {
    justify-content: center;
  }

  .footer__extras {
    padding-top: 0;
    width: auto;
    margin-bottom: 0;
    box-shadow: none;
  }
}
