.header {
  display: flex;
  align-items: center;
  padding-top: 36px;
  padding-bottom: 32px;
}

.header__logo {
  margin-right: 70px;
  display: flex;
  align-items: center;
}

.header__btn-menu {
  display: none;
}

.header__main-nav {
  margin-left: auto;
  margin-right: 37px;
}

.header__main-nav-list {
  @include reset-list();
  display: flex;
  flex-wrap: wrap;
}

.header__main-nav-item {
  margin-right: 40px;
}

.header__main-nav-link {
  @include font-size(14px, 17px, 500);
  text-decoration: none;
  color: $dark;
  letter-spacing: normal;
  padding-top: 6px;
  padding-bottom: 9px;

  &:hover {
    color: $theme;
  }

  &:active {
    text-decoration: underline;
  }
}

.header__main-nav-icon {
  display: none;
}

/* QUERRIES
===================================================================== */
@media screen and (max-width: $screen--md) {
  .header {
    justify-content: space-between;
    padding-top: 0;
    padding-bottom: 0;
    padding-right: 0;
    position: relative;

    &.no-js {
      flex-wrap: wrap;
      background-color: $white;
    }

    &.no-js .header__logo {
      display: none;
    }

    &.no-js .header__btn-menu {
      display: none;
    }

    &.no-js .header__main-nav {
      display: block;
      opacity: 1;
      z-index: 10;
    }
  }

  .header__logo-img {
    width: 44px;
  }

  .header__btn-menu {
    display: initial;
    padding: 9px 21px 8px;
    width: initial;
    margin: 0;

    &.active {
      z-index: 12;
      position: fixed;
      top: 0;
      right: 0;
      padding: 15px;
      background-color: transparent;
    }

    &.active:hover {
      outline: 1px solid rgba($dark, 0.2);
      border-radius: 50%;
    }

    &.active .header__btn-menu-open-icon {
      display: none;
    }

    &.active .header__btn-menu-close-icon {
      display: initial;
    }
  }

  .header__btn-menu-close-icon {
    display: none;
  }

  .header__main-nav {
    z-index: -1;
    opacity: 0;
    display: none;
    position: relative;
    width: 100%;
    margin: 0;
    padding-top: 134px;
    padding-bottom: 134px;
    background-color: $white;
    transition: all 0.6s ease-in-out;

    &.active {
      z-index: 10;
      opacity: 1;
      position: fixed;
      display: block;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
    }
  }

  .header__main-nav-list {
    display: flex;
  }

  .header__main-nav-item {
    width: 100%;
    margin-right: 0;
    margin-bottom: 30px;
  }

  .header__main-nav-link {
    @include font-size(20px, 28px, 600);
    color: $dark2;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 17px 35px 17px 16px;
    background-color: $light-grey;

    &:active {
      color: $theme;
    }

    &:active .header__main-nav-icon {
      color: $theme;
    }
  }

  .header__main-nav-icon {
    display: initial;
  }

  .header__phone {
    display: none;
  }
}

@media screen and (max-width: $screen--sm) {
  .header__btn-menu {
    padding-left: 10px;
    padding-right: 10px;
  }
}
