.promo {
  margin-bottom: 205px;
  color: $white;
  background: #6488b6 url("../img/promo/promo--lg.jpg") no-repeat center / cover;

  @media only screen and (min-resolution: 2dppx) {
    background: #6488b6 url("../img/promo/promo@2x--lg.jpg") no-repeat center / cover;
  }
}

.promo__desc {
  padding: 184px 100px 295px 38.3%;
}

.promo__title {
  @include font-size(55px, 80px, 600);
  letter-spacing: -1.11px;
  margin-top: 0;
  margin-bottom: 30px;
}

.promo__text {
  margin-top: 0;
  margin-bottom: 31px;
  padding-right: 12px;
}

@keyframes promo-icon {
  from {
    transform: translateY(-12px);
  }

  to {
    transform: translateY(3px);
  }
}

.promo__link {
  position: relative;
  width: 340px;
  text-align: left;
  padding-right: 66px;

  &:hover .promo__link-icon {
    animation: 0.8s ease-in-out infinite promo-icon;
  }
}

.promo__link-icon {
  position: absolute;
  right: 40px;
  top: 50%;
  transform: translateY(-50%);
  fill: none;
}

/* QUERRIES
===================================================================== */
@media screen and (max-width: $screen--md) {
  .promo {
    margin-bottom: 80px;
    background: #6488b6 url("../img/promo/promo--md.jpg") no-repeat center / cover;

    @media only screen and (min-resolution: 2dppx) {
      background: #6488b6 url("../img/promo/promo@2x--md.jpg") no-repeat center / cover;
    }
  }

  .promo__desc {
    padding: 65px 185px 83px;
    text-align: center;
  }

  .promo__title {
    margin-bottom: 19px;
    font-size: 30px;
    line-height: 35px;
    letter-spacing: normal;
  }

  .promo__text {
    margin-bottom: 8px;
    padding-right: 0;
  }

  .promo__link {
    width: 250px;
  }
}

@media screen and (max-width: $screen--sm) {
  .promo {
    position: relative;
    background: none;

    &::after {
      content: "";
      z-index: -1;
      overflow: hidden;
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 235px;
      background: #6488b6 url("../img/promo/promo--sm.jpg") no-repeat center top / cover;

      @media only screen and (min-resolution: 2dppx) {
        background: #6488b6 url("../img/promo/promo@2x--sm.jpg") no-repeat center top / cover;
      }
    }
  }

  .promo__desc {
    padding: 81px 16px 0;
  }

  .promo__title {
    width: 230px;
    margin-left: auto;
    margin-right: auto;
    margin-bottom: 102px;
    font-size: 24px;
    line-height: 36px;
  }

  .promo__text {
    width: 288px;
    margin: 0 auto 16px;
    color: $grey;
  }

  .promo__link {
    width: 288px;
  }
}
