.prices {
  position: relative;
  z-index: 1;
  margin-top: -284px;
  margin-bottom: 109px;
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
}

.prices__desc {
  order: 3;
  align-self: center;
  max-width: 370px;
  flex-grow: 1;
  padding-left: 61px;
  padding-top: 14px;
  margin-bottom: 30px;
}

.prices__title {
  letter-spacing: normal;
}

.prices__text {
  margin-top: 0;
  margin-bottom: 60px;
  letter-spacing: normal;
  line-height: 27px;
}

.prices__appeal-text {
  @include font-size(22px, 34px, 500);
  color: $dark;
  letter-spacing: normal;
  display: block;
  padding-left: 42px;
  margin-bottom: 13px;
}

.prices__appeal-phone {
  text-decoration: none;
  color: $black;
  font-size: 20px;

  &:active {
    text-decoration: underline;
  }
}

.prices__appeal-phone svg {
  margin-right: 25px;
  color: $theme;
}

.prices__card {
  max-width: 370px;
  flex-grow: 1;
  display: flex;
  flex-direction: column;
  margin-right: 30px;
  margin-bottom: 30px;
  padding: 40px 30px 30px;
  outline: 1px solid $grey--30;
  border-radius: 6px;
  transition: all 0.2s;
  background-color: $white;

  &:hover {
    outline: 3px solid $theme;
  }

  &:hover .prices__card-title {
    color: $theme;
    letter-spacing: normal;
  }

  &:hover .prices__card-icon {
    color: $dark;
  }

  &:hover .prices__card-list {
    box-shadow: 0 -1px 0 $theme;
  }
}

.prices__card-title {
  @include font-size(28px, 38px, 600);
  letter-spacing: -1.27px;
  color: $dark--70;
  transition: all 0.8s;
  margin-top: 0;
  margin-bottom: 40px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.prices__card-title-price {
  @include font-size(25px, 34px, 700);
  letter-spacing: normal;
}

.prices__card-text {
  margin-top: 0;
  margin-bottom: 28px;
  letter-spacing: normal;
  line-height: 24px;
  color: $grey;
}

.prices__card-list {
  @include reset-list();
  line-height: 32px;
  letter-spacing: normal;
  color: $grey;
  margin-bottom: 15px;
  padding-top: 30px;
  box-shadow: 0 -1px 0 $grey--30;
  transition: all 0.6s;
}

.prices__card-item {
  position: relative;
  margin-bottom: 15px;
  padding-left: 30px;
}

.prices__card-icon {
  position: absolute;
  top: 19%;
  left: 0;
  color: $theme;
  transition: all 1s;
}

.prices__card-btn {
  width: 100%;
  margin-top: auto;
}

/* QUERRIES
===================================================================== */
@media screen and (max-width: $screen--md) {
  .prices {
    margin-top: 0;
    margin-bottom: 80px;
    justify-content: center;
  }

  .prices__desc {
    order: 1;
    max-width: 100%;
    padding-left: 0;
    text-align: center;
    margin-bottom: 40px;
  }

  .prices__text {
    margin-bottom: 30px;
  }

  .prices__appeal {
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  .prices__appeal-text {
    font-size: 18px;
    padding-left: 0;
  }

  .prices__appeal-phone {
    font-size: 18px;
    color: $theme;
    text-decoration: underline;

    &:hover {
      text-decoration: none;
    }

    &:active {
      text-decoration: underline;
    }
  }

  .prices__appeal-phone-icon {
    display: none;
  }

  .prices__card {
    width: 288px;
    margin-right: 0;
    padding-top: 27px;
    padding-bottom: 0;
    padding-left: 20px;
    padding-right: 20px;
    flex-grow: initial;
    border-bottom-right-radius: 0;
    border-bottom-left-radius: 0;
  }

  .prices__card--full {
    order: 2;
    margin-right: 50px;
  }

  .prices__card--economy {
    order: 3;
  }

  .prices__card-title {
    font-size: 24px;
    flex-direction: column;
    margin-bottom: 11px;
  }

  .prices__card-title-price {
    margin-top: 10px;
  }

  .prices__card-text {
    margin-bottom: 10px;
    text-align: center;
  }

  .prices__card-list {
    margin-bottom: 18px;
    padding-top: 20px;
  }

  .prices__card-item {
    margin-bottom: 10px;
  }

  .prices__card-btn {
    width: 288px;
    margin-left: -21px;
  }
}

@media screen and (max-width: $screen--sm) {
  .prices {
    margin-bottom: 12px;
    padding-left: 12px;
    padding-right: 12px;
  }

  .prices__desc {
    padding-left: 4px;
    padding-right: 4px;
  }

  .prices__text {
    margin-bottom: 20px;
  }

  .prices__appeal-text {
    margin-bottom: 10px;
  }

  .prices__card {
    margin-left: 4px;
    margin-right: 4px;
  }

  .prices__card-list {
    margin-bottom: 27px;
  }
}
