html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

.index-page {
  height: 100%;
  scroll-behavior: smooth;
}

.index-page__body {
  @include font-size(16px, 28px, 400);
  display: grid;
  grid-template-rows: auto 1fr auto;
  position: relative;
  min-width: $min-width;
  max-width: $max-width;
  min-height: 100%;
  margin: 0 auto;
  padding: 0;
  font-family: $font;
  font-style: normal;
  color: $grey--70;
  letter-spacing: -0.47px;
  background-color: $white;
  overflow-x: hidden;
}

img {
  max-width: 100%;
  height: auto;
}

.v-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
}

[hidden] {
  display: none !important; /* stylelint-disable-line declaration-no-important */
}

.overlay {
  z-index: -1;
  position: absolute;
  width: 100%;
  height: 100%;
  background: $dark--70;
  display: none;

  &.active {
    z-index: 10;
    display: block;
  }
}

// * {
//   box-shadow: 0 0 4px rgba(0, 0, 0, 0.6);
// }

.layout-center {
  @include center($screen--lg, $gap--lg);
}

/* QUERRIES
===================================================================== */
@media screen and (max-width: $screen--md) {
  .index-page__body {
    overflow: auto;
  }

  .layout-center {
    max-width: $screen--md;
    padding: 0 $gap--md;
  }
}

@media screen and (max-width: $screen--sm) {
  .layout-center {
    max-width: $screen--sm;
    padding: 0 $gap--sm;
  }
}
