.feedback {
  display: flex;
  justify-content: flex-end;
  padding-top: 70px;
  padding-bottom: 70px;
  margin-bottom: 100px;
  letter-spacing: normal;
  color: $dark;
  line-height: 24px;
  background: url("../img/form-contact.jpg") no-repeat 35px center;
  background-size: 844px 455px;

  @media only screen and (min-resolution: 2dppx) {
    background: url("../img/form-contact@2x.jpg") no-repeat 35px center;
    background-size: 844px 455px;
  }
}

.feedback__content {
  width: 656px;
  padding: 50px;
  text-align: center;
  background-color: $white;
  box-shadow: 0 4px 30px rgba(41, 54, 66, 0.1);
  border-radius: 5px;
}

.feedback__title {
  @include font-size(25px, 32px, 600);
  margin-top: 0;
  margin-bottom: 15px;
}

.feedback__text {
  margin-top: 0;
  margin-bottom: 24px;
}

.feedback__form {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}

.feedback__form-group {
  margin-bottom: 25px;
}

.feedback__btn {
  margin: 0 auto;
  padding-top: 18px;
  padding-bottom: 17px;
}

/* QUERRIES
===================================================================== */
@media screen and (max-width: $screen--md) {
  .feedback {
    justify-content: center;
    padding: 70px 55px 74px;
    margin-bottom: 60px;
    background-size: cover;
    background-position: center;
  }

  .feedback__form-group {
    margin-bottom: 20px;
  }
}

@media screen and (max-width: $screen--sm) {
  .feedback {
    padding: 0;
    margin-bottom: 40px;
  }

  .feedback__content {
    width: 320px;
    padding: 50px 16px;
    box-shadow: none;
    border-radius: 0;
    background-color: $light-grey;
  }

  .feedback__title {
    margin-bottom: 20px;
  }

  .feedback__text {
    margin-bottom: 20px;
  }

  .feedback__input {
    width: 288px;
  }

  .feedback__btn {
    padding-top: 22px;
    padding-bottom: 19px;
  }
}
